.icon {
    width:.6rem;
    height:.6rem;
    vertical-align: -0.15em;
    fill: currentColor;
    overflow: hidden;
}

.toast-notification {
    position: fixed;
    top: 50%;
    left:50%;
    display: flex;
    flex-direction: column;
    z-index:10000;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%)
}

.toast-notice-wrapper.notice-enter {
    opacity:0.01;
    -webkit-transform:scale(0);
            transform:scale(0);
}
.toast-notice-wrapper.notice-enter-active {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-transition: all 300ms ease-out;
    transition: all 300ms ease-out;
}
.toast-notice-wrapper .notice-exit {
    opacity: 1;
    -webkit-transform: translateY(1);
            transform: translateY(1);
}

.toast-notice-wrapper.notice-exit-active{
    opacity: 0.01;
    -webkit-transform: translateY(0);
            transform: translateY(0);
    -webkit-transition: all 300ms ease-out;
    transition: all 300ms ease-out;
}
.toast-notice {
    background: #FFFFFF;
    padding: .2rem .4rem;
    margin: .1rem auto;
    box-shadow: 0rem .1rem .3rem 0rem rgba(0, 0, 0, .1);
    border-radius: .1rem;
    color: #454545;
    font-size: .4rem;
    display: flex;
    align-items: center;
}

.toast-notice>span {
    margin-left: .1rem;
    line-height: 100%;
}

.toast-notice>svg {
    font-size: .2rem;
}

.toast-notice.info>svg {
    color: #1890FF;
}

.toast-notice.success>svg {
    color: #52C41A;
}

.toast-notice.warning>svg {
    color: #FAAD14;
}

.toast-notice.error>svg {
    color: #F74A53;
}

.toast-notice.loading>svg {
    color: #1890FF;
    -webkit-animation: rotating 1s linear infinite;
            animation: rotating 1s linear infinite;

}

@-webkit-keyframes rotating {
    0% {
        -webkit-transform: rotate(0);
                transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes rotating {
    0% {
        -webkit-transform: rotate(0);
                transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
.laoding {
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background: #fff;
    z-index: 99;
}

.loading-style-4 {
    width: 1.33333rem;
    height: 1.33333rem;
    position: fixed;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    z-index: 99999;
}

.loading-style-4 span {
    display: inline-block;
    width: .266667rem;
    height: .266667rem;
    border-radius: 50%;
    background: lightgreen;
    position: absolute;
    -webkit-animation: loading-style-4 1.04s ease-in-out infinite;
    animation: loading-style-4 1.04s ease-in-out infinite;
}

@-webkit-keyframes loading-style-4 {
    0% {
        -webkit-transform: scale(1.2);
        opacity: 1;
    }

    100% {
        -webkit-transform: scale(.3);
        opacity: 0.5;
    }
}

@keyframes loading-style-4 {
    0% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
        opacity: 1;
    }

    100% {
        -webkit-transform: scale(.3);
                transform: scale(.3);
        opacity: 0.5;
    }
}

.loading-style-4 span:nth-child(1) {
    left: 0;
    top: 50%;
    margin-top: -10px;
    -webkit-animation-delay: 0.13s;
    animation-delay: 0.13s;
}

.loading-style-4 span:nth-child(2) {
    left: 14px;
    top: 14px;
    -webkit-animation-delay: 0.26s;
    animation-delay: 0.26s;
}

.loading-style-4 span:nth-child(3) {
    left: 50%;
    top: 0;
    margin-left: -10px;
    -webkit-animation-delay: 0.39s;
    animation-delay: 0.39s;
}

.loading-style-4 span:nth-child(4) {
    top: 14px;
    right: 14px;
    -webkit-animation-delay: 0.52s;
    animation-delay: 0.52s;
}

.loading-style-4 span:nth-child(5) {
    right: 0;
    top: 50%;
    margin-top: -10px;
    -webkit-animation-delay: 0.65s;
    animation-delay: 0.65s;
}

.loading-style-4 span:nth-child(6) {
    right: 14px;
    bottom: 14px;
    -webkit-animation-delay: 0.78s;
    animation-delay: 0.78s;
}

.loading-style-4 span:nth-child(7) {
    bottom: 0;
    left: 50%;
    margin-left: -10px;
    -webkit-animation-delay: 0.91s;
    animation-delay: 0.91s;
}

.loading-style-4 span:nth-child(8) {
    bottom: 14px;
    left: 14px;
    -webkit-animation-delay: 1.04s;
    animation-delay: 1.04s;
}

